.form-row {
	position: relative;
	display: flex;
	flex-wrap: wrap;

	label.error {
		margin-top: 0.3rem;

		+.form-row__btn {
			background-color: $red;
			border-color: $red;
		}
	}

	// .subscribe-form__input

	&__input {
		@extend %vertical-align;
		@extend %text-14;
		width: 100%;
		height: 5.2rem;
		border: 0.1rem solid #101112;
		border-right: none;
		padding: 0 6.8rem 0 1.6rem;

		&::placeholder {
			color: #777;
		}

		&.error {
			border-color: $red;
		}
	}

	// .subscribe-form__btn

	&__btn {
		position: absolute;
		top: 0;
		right: 0;
		width: 5.2rem;
		height: 5.2rem;
		background-color: #101112;
		border: 0.1rem solid #101112;
		transition: .3s all;

		@media (any-hover: hover) {
			&:hover {
				background-color: $green;
				border-color: $green;
			}
		}

		&:before {
			content: '';
			position: absolute;
			@extend %pos-center-xy;
			width: 2.3rem;
			height: 1.6rem;
			background-image: url(../images/icons/submit-arr.svg);
			@extend %bg-contain;
		}
	}
}

.form-agree {
	display: flex;
	align-items: flex-start;
	cursor: pointer;

	.check-style {
		width: 2rem;
		height: 2rem;
	}

	.check-box:checked+.check-style:before {
		width: 1.3rem;
		height: 1rem;
	}

	.check-text {
		@extend %text-12;
		margin-left: 1rem;
	}
}

.subscribe-form {

	// .subscribe-form__wrap

	&__wrap {
		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}
}

.input-wrap {
	position: relative;

	.input {
		width: 100%;
	}
}

.input {
	@extend %text-13;
	border: 0.1rem solid #C0C0C0;
	padding: 1.4rem;

	&::placeholder {
		color: #777;
	}
}

.pass-btn {
	@extend %bg-cover;
	position: absolute;
	top: 1.2rem;
	right: 1.2rem;
	width: 2.4rem;
	height: 2.4rem;

	&.show {
		background-image: url(../images/icons/hidden.svg);
	}

	&.hide {
		background-image: url(../images/icons/visible.svg);
	}
}
