.back-link {
	@extend %text-14;
	color: $green;
	display: inline-flex;
	align-items: flex-start;

	svg {
		width: 2rem;
		height: 2rem;
		margin-right: 0.8rem;
	}

	@media (any-hover: hover) {
		&:hover {
			color: #2D703C;

			svg path {
				fill: #2D703C;
			}
		}
	}
}

.profile-page {

	padding-bottom: 11rem;

	@media(max-width: 500px) {
		padding-bottom: 5rem;
	}

	// .profile-page__inner

	&__inner {
		display: grid;
		grid-gap: 3rem;

		@media(min-width: 1025px) {
			grid-template-columns: max-content 98.3rem;
			justify-content: space-between;
		}
	}

	&__top {
		@extend .profile-page__inner;
		align-items: flex-end;
		margin-bottom: 4rem;

		.page__title {
			margin-bottom: 0;
		}
	}

	// .profile-page__title

	&__title {
		@extend %text-20;
		font-weight: 500;
		padding-bottom: 2rem;
		border-bottom: 0.1rem solid #EAEAEA;

		&:not(:last-child) {
			margin-bottom: 4rem;

			@media(max-width: 500px) {
				margin-bottom: 3rem;
			}
		}
	}

	// .profile-page__content

	&__content {
		section {
			&:not(:last-child) {
				padding-bottom: 3rem;
			}
		}

		p {
			@extend %text-16;

			@media(max-width: 500px) {
				font-size: 1.4rem;
				line-height: 2.1rem;
			}

			&:not(:last-child) {
				margin-bottom: 1.5rem;
			}

			b {
				font-weight: 600;
			}
		}

		h3 {
			@extend %text-17;
			font-weight: 600;

			&:not(:last-child) {
				margin-bottom: 2rem;
			}
		}

		h4 {
			@extend %text-16;
			font-weight: 600;

			&:not(:last-child) {
				margin-bottom: 1rem;
			}
		}

		ul li {

			@extend %text-16;

			@media(max-width: 500px) {
				font-size: 1.4rem;
				line-height: 2.1rem;
			}

			&:not(:last-child) {
				margin-bottom: 1.5rem;
			}

			b {
				font-weight: 600;
			}
		}
	}

	// .profile-page__tabs

	&__tabs {

		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}

	// .profile-page__tab

	&__tab {
		padding-bottom: 0.9rem;

		&:not(:last-child) {
			margin-right: 7rem;
		}
	}

	&__order-table {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.profile-page-goods {
	&__title {
		&:not(:last-child) {
			margin-bottom: 3.5rem;
		}
	}

	&__list {
		display: grid;
		grid-gap: 3rem 8.2rem;

		@media(max-width: 1024px) {
			grid-gap: 3rem;
		}

		@media(min-width: 501px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}
}

.about-page {

	h4 {
		&~* {
			&:not(:last-child) {
				margin-bottom: 3rem;

				@media(max-width: 500px) {
					margin-bottom: 2rem;
				}
			}
		}
	}

	ul li {
		position: relative;
		padding-left: 1.7rem;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 1.2rem;
			width: 1.2rem;
			height: 1px;
			background-color: #101112;
		}
	}
}

.delivery {
	>section {
		&:not(:last-child) {
			padding: 4rem 0;
		}

		&:first-child {
			padding-top: 0;
		}

		&:last-child {
			padding-top: 4rem;
		}

		&:not(:last-child) {
			position: relative;

			&:after {
				content: '';
				position: absolute;
				left: 0;
				bottom: 0;
				width: 854 / 983 * 100%;
				height: 0.1rem;
				background-color: #EAEAEA;
			}
		}
	}
}
