// Reset
// ==========================================================================
// Reset default styles.
// Add border box sizing in all browsers (opinionated).

* {
	box-sizing: inherit;
	margin: 0;
	padding: 0;

	&:before,
	&:after {
		box-sizing: inherit;
	}
}

// Normalize vertical alignment of `progress` in Chrome, Firefox, and Opera.
audio,
canvas,
progress,
video {
	vertical-align: baseline;
}

// Prevent modern browsers from displaying `audio` without controls.
// Remove excess height in iOS 5 devices.
audio:not([controls]) {
	display: none;
	height: 0;
}

// Hide the `template` element in IE, Safari, and Firefox < 22.
template {
	display: none;
}

address {
	font-style: normal;
}

// Base
// ==========================================================================
// 1. Set default font family to sans-serif.
// 2. Prevent iOS text size adjust after orientation change, without disabling user zoom.
// 3. Remove highlight on mobile devices.
// 4. Set default cursor.
// 5. Breaks words to prevent overflow in all browsers (opinionated).
html {
	font-family: sans-serif; // 1
	word-break: break-word; // 5
	-ms-text-size-adjust: 100%; // 2
	-webkit-text-size-adjust: 100%; // 2
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0); // 3
	-webkit-focus-ring-color: rgba(255, 255, 255, 0); // 3
	cursor: default;
}

// Set 100% line-height
body {
	font-size: 100%;
	line-height: 1;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
}

// Links
// ==========================================================================
// Improve readability when focused and also mouse hovered in all browsers.
*:focus,
*:active,
*:hover {
	outline: none;
}

// Typography
// ==========================================================================
// Address differences between Firefox and other browsers.
hr {
	box-sizing: content-box;
	height: 0;
}

// Improve readability of pre-formatted text in all browsers.
pre {
	white-space: pre-wrap;

	tab-size: 4;
}

// Set consistent quote types.
q {
	quotes: '«''»';
}

// Forms
// ==========================================================================
// Address Firefox 4+ setting `line-height` on `input` using `!important` in the UA stylesheet.
button,
input {
	line-height: normal;
}

// Remove the margin on controls in Safari.
button,
input,
select {
	margin: 0;
}

// Address inconsistent `text-transform` inheritance for `button` and `select`.
// All other form control elements do not inherit `text-transform` values.
// Correct `button` style inheritance in Chrome, Safari 5+, and IE 8+.
// Correct `select` style inheritance in Firefox 4+ and Opera.
button,
select {
	text-transform: none;
}

// 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio` and `video` controls.
// 2. Correct inability to style clickable `input` types in iOS.
// 3. Improve usability and consistency of cursor style between image-type `input` and others.
button,
html input[type='button'],
// 1
input[type='reset'],
input[type='submit'] {
	cursor: pointer; // 3
	-webkit-appearance: button; // 2
}

// Re-set default cursor for disabled elements.
button[disabled],
html input[disabled] {
	cursor: default;
}

// Fix the cursor style for Chrome's increment/decrement buttons. For certain
// `font-size` values of the `input`, it causes the cursor style of the
// decrement button to change from `default` to `text`.
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
	height: auto;
}

// 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
// 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome (include `-moz` to future-proof).
input[type='search'] {
	box-sizing: content-box;
	-webkit-appearance: textfield; // 1
}

// Remove inner padding and search cancel button in Safari 5 and Chrome on OS X.
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
	-webkit-appearance: none;
}

// Improve readability and alignment in all browsers.
textarea {
	vertical-align: top;
}

// Reset unusual Firefox-on-Android default style.
button,
input,
select[multiple],
textarea {
	background-image: none;
}

// Remove `border-radius` and `box-shadow` in iOS.
input,
select,
textarea {
	border-radius: 0;
	box-shadow: none;
}

// 1. Disable resize.
// 2. Allow user select.
input,
textarea {
	resize: none; // 1
	user-select: text; // 2
}

fieldset {
	border: none;
	margin: 0;
}

// Reset form input styles for iOS
button,
textarea,
select,
input[type="text"],
input[type="email"],
input[type="password"],
input[type="search"],
input[type="submit"],
input[type="button"],
input[type="reset"],
input[type="number"],
input[type="range"] {
	border-radius: 0;
	appearance: none;

	&::-ms-clear,
	&::-ms-reveal {
		display: none;
	}
}

// Hide placeholder on focus.
[placeholder]:focus::placeholder {
	color: transparent;
}

// Tables
// ==========================================================================
// Remove most spacing between table cells.
table {
	border-collapse: collapse;
	border-spacing: 0;
}

// Iframes
// ==========================================================================
// Remove border from iframe
iframe {
	border: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
}

ul,
ol,
li {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

dl,
dd,
dt {
	margin: 0;
	padding: 0;
}

a {
	text-decoration: none;
	color: inherit;
}

select,
button,
input {
	background-color: transparent;
	border: none;
	outline: none;
	padding: 0;
	cursor: pointer;
}

input,
textarea {
	cursor: default;
}
