.product-details {

	// .product-details__section

	&__section {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}

	// .product-details__section-top

	&__section-top {
		@extend %flex-row-center;
		padding-bottom: 1rem;
		border-bottom: 0.1rem solid #EAEAEA;
	}

	// .product-details__section-title

	&__section-title {
		@extend %text-16;
		font-weight: 600;
	}

	// .product-details__hide-btn

	&__hide-btn {
		position: relative;
		@extend %text-13;
		color: #505661;
		padding-right: 2.5rem;
		text-transform: lowercase;
		white-space: nowrap;
		transition: .3s color;

		@media(max-width: 500px) {
			padding-right: 2rem;
		}

		@media (any-hover: hover) {
			&:hover {
				color: $green;
			}
		}

		&:after {
			content: '';
			position: absolute;
			top: 0.2rem;
			right: 0;
			width: 1.4rem;
			height: 1.5rem;
			background-image: url(../images/icons/drop.svg);
			@extend %bg-contain;
			transition: .3s transform;
		}

		&--active {
			&:after {
				transform: rotate(180deg);
			}
		}
	}

	// .product-details__list-item

	&__list-item {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		align-items: center;
		padding: 0.6rem 0;
		@extend %text-13;
		border-bottom: 0.1rem dashed #EAEAEA;

		@media(max-width: 500px) {
			grid-template-columns: 60% 40%;
			grid-gap: 2rem;
		}

		dt {
			@extend %vertical-align;
		}

		dd {
			font-weight: 600;
		}

		p {
			max-width: 26.7rem;
		}
	}

	// .product-details__icons-list

	&__icons-list {
		display: flex;

		li {
			&:not(:last-child) {
				margin-right: 1.1rem;
			}
		}
	}
}

.tooltip {

	position: relative;
	margin-left: 0.6rem;
	flex-shrink: 0;
	// .tooltip__btn

	&__btn {
		@extend %vertical-align;

		svg {
			width: 1.7rem;
			height: 2.1rem;

			path {
				&:nth-child(1) {
					transition: .3s stroke;
				}
			}

			path {
				&:nth-child(2) {
					transition: .3s fill;
				}
			}
		}

		@media (any-hover: hover) {
			&:hover {
				path {
					&:nth-child(1) {
						stroke: $green;
					}

					&:nth-child(2) {
						fill: $green;
					}
				}
			}
		}
	}

	// .tooltip__content

	&__content {
		display: none;
		position: absolute;
		top: 0;
		left: calc(100% + 0.7rem);
		z-index: 25;
		width: 19.8rem;
		padding: 1rem;
		background-color: #FFF;
		border: 0.1rem solid #EAEAEA;
		box-sizing: border-box;
		box-shadow: 0rem 0.1rem 2.8rem rgba(0, 0, 0, 0.07);
		border-radius: 0.4rem;
		@extend %text-10;
		font-weight: 500;
		color: #505661;
	}
}
