.sale-item {

	// .sale-item__img

	&__img {
		position: relative;
		display: block;
		height: 42rem;

		img {
			@extend %cover;
		}

		margin-bottom: 1.2rem;
	}

	&__discount {
		@extend %text-40;
		font-weight: 500;
		text-transform: uppercase;
		color: #fff;
		position: absolute;
		top: 2.5rem;
		left: 2.5rem;

		@media(max-width: 500px) {
			top: 1.5rem;
			left: 1.5rem;
		}
	}

	// .sale-item__title

	&__title {
		@extend %text-20;
		font-weight: 500;
	}

	// .sale-item__descr

	&__descr {
		@extend %text-16;
		color: #777;
	}
}
